export const getAttributeValue = (hostElement, attributeName) => {
	if (!hostElement || !attributeName)
		return '';

	const camelCase = attributeName.replace(/-([a-z])/g, (g) => g[1].toUpperCase());
	return hostElement.getAttribute(attributeName) || hostElement.getAttribute(camelCase) || hostElement[camelCase];
};

// return the refresh frequency in millis
export const getParsedFrequencyInMillis = (refreshFrequencyParam) => {
	const refreshFrequency = refreshFrequencyParam * 1; // its the fastest way to convert string to number check jspref for more
	if (isNaN(refreshFrequency) || !(Math.sign(refreshFrequency) >= 0) || (refreshFrequency === Infinity))
		return 0;

	return Math.round(refreshFrequency) * 1000;
};
