/**
 * Builder pattern used for building the payload. The initial payload can be conditionally enriched.
 *
 * The builder keeps current state of the payload being built and passes it to the next add function
 * @param initialPayload - the initial payload used as a base of the builder
 * @returns {object} - add function is used for enriching the payload
 * object with new properties, build is building the final object.
 */
export const payloadBuilder = (initialPayload) => {
	let state = initialPayload;

	function add(addingFunction, condition = true) {
		if(condition) {
			state = {
				...state,
				...addingFunction(state)
			};
		}

		return this;
	}

	return {
		add,
		build() {
			return state;
		}
	};
};
