import get from 'lodash/get';
import { combineQueries } from '../helpers/combine-queries';
import { isPieDonut, isTimeSeries } from '../helpers/chart-types';
import { dateHelper } from 'par-components';

/**
* Get the right series object from the API response's payload
* @param {Object} pointData - object containing the x and all the Y values of the datapoint
* @param {Object} state - Visualization's state object
* @returns {Object}
*/
const getSeriesFromConfig = (pointData, state) => {
	let seriesKey;

	// get the series key name from Xenolith point's y key
	Object.keys(pointData).forEach(key => {
		if (key.indexOf('data_') > -1 && key.indexOf('percent') === -1)
			seriesKey = key;
	});

	// TODO: Add support for datapoint selection on multi-series once it gets implemented by Xenolith
	return get(state, `payload.seriesConfig.${seriesKey}`);
};

/**
 * Get uuid, either from elementUuids in case of groupBy charts, or from series config
 * @param {Object} seriesConfig - Series config of the clicked series
 * @param {Object} clickData - Click data passed from Xenolith
 * @returns {Object}
 */
function getUuid(seriesConfig, clickData) {
	return get(seriesConfig, `elementUuids.${clickData.x}`) || 	get(seriesConfig, 'uuid');
}

/**
 * Generate action payload for pie/donut/semi-donut charts
 * @param {Object} actionPayload - Xenolith action payload with data point information
 * @param {Object} state - Visualization's state object
 * @returns {Object}
 */
function getPieDonutActionPayload(actionPayload, state) {
	const clickData = get(actionPayload, 'data');

	const seriesConfig = getSeriesFromConfig(clickData, state);

	const conditions = combineQueries(get(seriesConfig, `aggregateQueries.${clickData.x}`), get(seriesConfig, 'filterQuery'));

	return conditions === 'zzyynomatchaabb' ?
		{} // mute, don't pass any payload when clicking on 'other' section, identified by 'zzyynomatchaabb' string
		: {
			...actionPayload,
			type: state.chartType,
			table: get(seriesConfig, 'table_name'),
			uuid: getUuid(seriesConfig, clickData),
			label: get(seriesConfig, 'label'),
			conditions
		};
}

/**
 * Generate action payload for time series and bar charts
 * @param {Object} actionPayload - Xenolith action payload with data point information
 * @param {Object} state - Visualization's state object
 * @returns {Object}
 */
function getActionPayload(actionPayload, state) {
	const pointData = get(actionPayload, 'data.seriesDataset') || actionPayload.coords;
	let closestSeries = get(actionPayload, 'data.closestSeries');
	let series;

	if (closestSeries) {
		// TODO: remove these checks once NDS delivers the fixes
		if (Array.isArray(closestSeries))
			closestSeries = closestSeries[0];

		if (closestSeries.substring(0, 6) === 'series')
			closestSeries = closestSeries.substring(6);

		// if forecast was clicked get the series name from forecast config
		if (get(actionPayload, 'data.isForecast'))
			closestSeries = get(state, `payload.forecastConfig.${closestSeries}.for`);

		series = get(state, `payload.seriesConfig.${closestSeries}`);
	} else
		series = getSeriesFromConfig(pointData, state);

	let scoreDate;

	if(isTimeSeries(state.chartType))
		scoreDate = dateHelper.getMoment(parseInt(pointData.x)).format('YYYY-MM-DD');

	const conditions = combineQueries(get(series, `aggregateQueries.${pointData.x}`), get(series, 'filterQuery'));

	return conditions === 'zzyynomatchaabb' ?
		{} // mute, don't pass any payload when clicking on 'other' section, identified by 'zzyynomatchaabb' string
		: {
			...actionPayload,
			type: state.chartType,
			table: get(series, 'table_name'),
			uuid: getUuid(series, pointData),
			label: get(series, 'label'),
			conditions,
			scoreDate
		};
}

/**
* Extend action dispatched on click from Xenolith with additinal PAR properties on
* POINT_CLICKED action's payload.
* @param {Object} actionPayload - Xenolith action payload with data point information
* @param {Object} state - Visualization's state object
* @returns {Object}
*/
export default (actionPayload, state) => {
	// TODO: change this once Xenolith standardizes payload format accross the chart types.
	if(isPieDonut(state.chartType))
		return getPieDonutActionPayload(actionPayload, state);

	return getActionPayload(actionPayload, state);
};
