import { get, isEmpty } from 'lodash';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { t } from 'sn-translate';
import {
	SERIES_FETCH_REQUESTED,
	SERIES_FETCH_DATACONFIG_REQUESTED,
	CHARTS_DATACONFIG_REQUESTED,
	SERIES_FETCH_STARTED,
	SERIES_FETCH_SUCCEEDED,
	AUTO_REFRESH_SUBSCRIBE,
	CHARTS_DATA_FETCH_SUCCEEDED,
	POINT_CLICKED_XENOLITH,
	POINT_CLICKED,
	ELEMENT_VISIBLE,
	RECORD_WATCHER_METADATA_REQUESTED,
	CHARTDATA_LOADED,
	FETCH_SERIES,
	RECORD_WATCHER_SUBSCRIBED,
	HTTP_ERROR_OCCURRED,
} from '../constants';
import { parseServerChartConfiguration } from 'par-visualization/seismic';
import addParActionProperties from '../interactions/add-par-action-props';
import { trimTarget } from '../helpers/preprocess-series-data';
import adaptConfigType from '../adapters/adapt-config-type';
import { isTimeSeries, isPieDonut, isBarType, isSingleScore } from '../helpers/chart-types';
import effectProxy  from '../helpers/seismic/effect-proxy';

export const handleSeriesFetchRequested = createHttpEffect('/api/now/v2/reporting/reports/series/:sysId', {
	method: 'POST',
	pathParams: ['sysId'],
	batch: false,
	startActionType: SERIES_FETCH_STARTED,
	successActionType: SERIES_FETCH_SUCCEEDED,
	errorActionType: SERIES_FETCH_SUCCEEDED,
});

export const handleSeriesFetchDataConfigRequested = createHttpEffect('/api/now/v2/reporting/reports/series', {
	method: 'POST',
	dataParam: 'dataConfigurationsReqBody',
	batch: false,
	startActionType: SERIES_FETCH_STARTED,
	successActionType: SERIES_FETCH_SUCCEEDED,
	errorActionType: SERIES_FETCH_SUCCEEDED,
});

export const handleChartsDataConfigRequested = createHttpEffect('/api/now/charts/data', {
	method: 'POST',
	dataParam: 'dataConfigurationsReqBody',
	batch: false,
	startActionType: SERIES_FETCH_STARTED,
	successActionType: CHARTS_DATA_FETCH_SUCCEEDED,
	errorActionType: CHARTS_DATA_FETCH_SUCCEEDED,
});

/**
 * This effect is called when the fetching starts
 */
const handleSeriesFetchStarted = {
	effect({ state, action, updateState }) {
		updateState({
			...state,
			loading: !action.meta.hideLoading, // from autorefresh context don't show loading indicator
			error: false,
			fetchedBySysId: action.meta.sysId
		});
	}
};

export const handleChartsDataFetchFinished = {
	effect({ state, action, updateState, dispatch }) {
		if (action.error) {
			updateState({
				...state,
				loading: false,
				error: {
					status: get(action.payload, 'status'),
					...get(action.payload, 'data.error', t('Error fetching series'))
				},
			});

			return;
		}

		const { chartType, payload, filters } = action.payload.result;
		const { dataConfigurationsReqBody } = action.meta;

		if (isSingleScore(chartType)) {
			dispatch(AUTO_REFRESH_SUBSCRIBE, {
				dispatcherActionName: CHARTS_DATACONFIG_REQUESTED,
				dispatcherActionPayload: { dataConfigurationsReqBody },
			});
		}

		dispatch(CHARTDATA_LOADED, {
			filters
		});

		if(payload.realTimeSubscriptionChannel) {
			const channelId = payload.realTimeSubscriptionChannel;

			dispatch(RECORD_WATCHER_SUBSCRIBED, {
				channelId,
				subscribe: true,
			});

			updateState({ channelId });
		}

		updateState({
			...state,
			loading: false,
			error: false,
			chartType,
			isRequestCompleted: true,
			payload
		});
	}
};

/**
 * This effect is called when the fetching is done (regardless of an error occurring)
 */
const handleSeriesFetchFinished = {
	effect({ state, action, updateState, dispatch, host }) {
		dispatch(CHARTDATA_LOADED, {});

		if (action.error) {
			updateState({
				...state,
				loading: false,
				error: {
					status: get(action.payload, 'status'),
					...get(action.payload, 'data.error', t('Error fetching series'))
				}
			});

			return;
		}

		// Get the properties
		const {
			properties,
			properties: { sysId, isRealtime }
		} = state;

		const { chartType, payload } = action.payload.result;

		// FIXME: this logic needs to be revisited and rewritten!
		if (
			isTimeSeries(chartType) ||
			isBarType(chartType) ||
			isPieDonut(chartType)
		) {
			updateState({
				...state,
				loading: false,
				error: false,
				chartType,
				payload
			});

			return;
		}

		const v1Payload = {
			result: payload
		};

		// Get all the needed data
		const chartConfiguration = parseServerChartConfiguration(v1Payload);
		const dataConfigurations =
			chartConfiguration.data_configurations || properties.dataConfigurations;
		let styleConfigurations =
			chartConfiguration.style_configurations || properties.styleConfigurations;
		const datasets = chartConfiguration.datasets || properties.datasets;

		if (
			isEmpty(chartConfiguration.style_configurations) &&
			!isEmpty(properties.styleConfigurations)
		)
			styleConfigurations = [...properties.styleConfigurations];

		// If there is no data configurations, then don't even bother
		if (!dataConfigurations || !dataConfigurations.length) {
			updateState({
				...state,
				loading: false,
				error: t('Series not found')
			});

			return;
		}

		const targets = get(datasets, '[0].targets');

		if (targets) {
			const sparkData = get(datasets, '[0].sparkdata', []);
			const lastXData = get(sparkData, `[${sparkData.length - 1}].x`);

			// Trim targets which are set beyond last score data
			datasets[0].targets.data = trimTarget(lastXData, targets.data);
		}

		const sparkline = get(datasets, '[0].sparkline');

		if (window.ResizeObserver && sparkline) {
			// Force Visualization component to update/reflow highcharts
			const resizeHandler = () => {
				updateState({
					refresh: !state.refresh
				});
			};

			const resizeObserver = new window.ResizeObserver(resizeHandler);
			resizeObserver.observe(host);
		}

		if (isRealtime)
			dispatch(RECORD_WATCHER_METADATA_REQUESTED, { sysId }, { sysId });
		else if (isSingleScore(chartType))
			handleAutoRefreshForDataConfig(sysId, action, dispatch);

		updateState({
			...state,
			dataConfigurations,
			styleConfigurations,
			datasets,
			loading: false,
			error: false,
			chartType,
			isRequestCompleted: true
		});
	}
};

export default {
	[SERIES_FETCH_REQUESTED]: effectProxy(handleSeriesFetchRequested),
	[SERIES_FETCH_DATACONFIG_REQUESTED]: {
		modifier: { name: 'takeLatest' },
		...handleSeriesFetchDataConfigRequested
	},
	[CHARTS_DATACONFIG_REQUESTED]: {
		modifier: { name: 'takeLatest' },
		effect: effectProxy(handleChartsDataConfigRequested)
	},
	[SERIES_FETCH_STARTED]: handleSeriesFetchStarted,
	[SERIES_FETCH_SUCCEEDED]: handleSeriesFetchFinished,
	// Just absorb the HTTP errors
	[HTTP_ERROR_OCCURRED]: { private: true },
	// Just a proxy fetch series event, proxies to either charts data or V2 Series API
	[FETCH_SERIES]: {
		modifier: { name: 'debounce', delay: 50 },
		effect ({ action: { payload }, dispatch }) {
			const config = { ...payload.dispatcherActionPayload };
			dispatch(payload.dispatcherActionName, config, config);
		}
	},
	[CHARTS_DATA_FETCH_SUCCEEDED]: handleChartsDataFetchFinished,
	[ELEMENT_VISIBLE]: {
		effect({ dispatch, state }) {
			const { dataConfigurations, filterConfigurations, sysId, chartType } = state.properties;

			if (sysId) {
				// We resend the series fetch. Sysid needs to be both in payload and in the meta
				dispatch(SERIES_FETCH_REQUESTED, { sysId }, { sysId });
			} else if (dataConfigurations && dataConfigurations.length) {

				// if chartType is passed hit the newer PAR API's Chart data endpoint otherwise use the PAR API v2
				if (chartType) {
					const dataConfigurationsReqBody = {
						data_configurations: dataConfigurations,
						filter_configurations: filterConfigurations,
						chart_type: chartType,
					};

					dispatch(CHARTS_DATACONFIG_REQUESTED, { dataConfigurationsReqBody }, { dataConfigurationsReqBody });
				} else {
					const dataConfigurationsReqBody = {
						data_configurations: adaptConfigType(dataConfigurations),
					};
					dispatch(SERIES_FETCH_DATACONFIG_REQUESTED, { dataConfigurationsReqBody }, { dataConfigurationsReqBody });
				}

			}
		},
	},
	[POINT_CLICKED_XENOLITH]: {
		effect({ action, dispatch, state }) {
			const clickPayload = addParActionProperties(action.payload, state);

			action.stopPropagation();
			dispatch(POINT_CLICKED, clickPayload, action.meta);
		},
	}
};

const handleAutoRefreshForDataConfig = (sysId, action, dispatch) => {
	let dispatcherActionPayload = {};
	if (!sysId)
		dispatcherActionPayload = { dataConfigurationsReqBody: action.meta.dataConfigurationsReqBody };
	else
		dispatcherActionPayload = { sysId };

	dispatch(AUTO_REFRESH_SUBSCRIBE, {
		dispatcherActionName: (sysId) ? SERIES_FETCH_REQUESTED : SERIES_FETCH_DATACONFIG_REQUESTED,
		dispatcherActionPayload,
	});
};
