/**
* Return the default locale in this order:
* - the browser locale
* - default 'en-US'
* Should work on all browsers according to: https://developer.mozilla.org/en-US/docs/Web/API/NavigatorLanguage/language
*
* @return {String}
*/

export const getDefaultLocale = () => {
	if (navigator && navigator.language)
		return navigator.language;

	return 'en-US';
};
