import get from 'lodash/get';

export default ({ state, dataConfigurations, styleConfigurations }) => {
	const { properties } = state;

	// Check if the user wants to disable click behavior
	const canClick = get(styleConfigurations, '[0].canClick', true);

	if(!canClick)
		return false;

	// If the auto click event is not there, then we always pass the click
	// behaviors
	if (!properties.autoClickBehavior || properties.kpiDetailsNavigation)
		return true;

	// First we check based on type
	const type = get(styleConfigurations, '[0].chart_type');
	const allowedTypes = ['score', 'single_score'];

	if (type && allowedTypes.indexOf(type) < 0)
		return false;

	// Now we check based on data configurations
	const dataConfiguration = get(dataConfigurations, '[0]', {});
	if (!dataConfiguration.table_name)
		return false;

	return true;
};
