/**
 * Filters out targets which are
 * out of range of score data
 * @param {Number} lastXdata date of last score available
 * @param {Array} targetsData targets to be filtered
 * 
 * @returns {Array} filtered targets
 */
export const trimTarget = (lastXdata, targetsData) => {
	return lastXdata && Array.isArray(targetsData) ?
		targetsData.filter(target => lastXdata >= target.x) : targetsData;
};
