import { getValueWithUnit } from '../helpers/get-value-with-unit';
import adaptXenolith from './adapt-xenolith';
import { t } from 'sn-translate';
import { defaultFormattersConfig } from '../formatters/default-formatters-config';

/**
 * Checks if the single score has score to display or not.
 * @param {Object} styleConfigurations - additional styling options
 * @param sourceType - source type
 * @param score - score value
 * @returns Returns true if there is no score, else false.
 */
export const isNoScore = (score) => {
	// In case the score is 0, !score would be evaluated as true. That's why the additional score === 0 check is necessary.
	return !(score || score === 0);
};

/**
 * Converts the response from the API into design system format for single score
 * @param {Object} payload - response from the api
 * @param {Object} styleConfigurations - additional styling options
 * @returns {Object} Adapted single score object, suitable for design system single score
 */
export const adaptSingleScore = (payload = {}, styleConfigurations = {}, targetRef = {}) => {
	const {
		sparkline,
		score,
		scoreColor,
		changeInfo,
		targetInfo,
		timestamp,
		heading,
	} = payload;

	const formattersConfig = defaultFormattersConfig(payload.formattersConfig);

	const scoreColorValue = styleConfigurations.scoreColor ?
		styleConfigurations.scoreColor :
		scoreColor;

	targetRef.style.setProperty('--now-score--value-color', scoreColorValue);

	if (isNoScore(score)) {
		return {
			score: t('No Score')
		};
	}

	if (changeInfo) {
		const changeColorValue = styleConfigurations.changeColor ?
			styleConfigurations.changeColor :
			changeInfo.color;
		targetRef.style.setProperty('--now-score--change-color', changeColorValue);
	}

	const calculatedScore = formattersConfig.isDuration ? score * 1000 : score;
	const displayedScore = !(score || score === 0) ?
		'0':
		formattersConfig.fxDisplayValue ? formattersConfig.fxDisplayValue : getValueWithUnit(calculatedScore, formattersConfig);

	return {
		heading,
		timestamp,
		score: displayedScore,
		changeInfo: changeInfo && buildChangeInfo(changeInfo, formattersConfig),
		targetInfo: targetInfo && buildTargetInfo(targetInfo, formattersConfig),
		sparkline: sparkline &&
			{ ...adaptXenolith(sparkline, sparkline.type, styleConfigurations) },
		canClick: true,
		hideWhitespace: true,
		...styleConfigurations,
	};
};

/**
 * Converts changeInfo property from the api format to design system format
 * @param {Object} changeInfo - property from the api
 * @param {Object} formattersConfig - used for applying different formatting options (locale, precision, unit...)
 * @returns {Object} - formatted changeInfo which can be used in the design system single score component
 */
const buildChangeInfo = (changeInfo, formattersConfig) => {
	const {
		change,
		arrow,
		timestamp,
		percentage,
	} = changeInfo;

	const changePercent = percentage ? ` (${percentage})` : '';

	return {
		change: (change || change === 0) && `${getValueWithUnit(change, formattersConfig)}${changePercent}`,
		timestamp,
		arrow,
	};
};

/**
 * Converts targetInfo property from the api format to design system format
 * @param {Object} targetInfo - property from the api
 * @param {Object} formattersConfig - used for applying different formatting options (locale, precision, unit...)
 * @returns {Object} - formatted targetInfo which can be used in the design system single score component
 */
const buildTargetInfo = (targetInfo, formattersConfig) => {
	const {
		target,
		gap,
		gapPercentage,
	} = targetInfo;

	const gapPercent = gapPercentage ? ` (${gapPercentage})` : '';

	return {
		target: (target || target === 0) && getValueWithUnit(target, formattersConfig),
		gap: gap && `${getValueWithUnit(gap, formattersConfig)}${gapPercent}`,
	};
};
