import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';

import info from './icons/info.svg';
import warning from './icons/warning.svg';

import styles from './snElementsFilterMessage.scss';

const title = {
	info: t('No Data Available'),
	warning: t('Error'),
};

function getMessageType(typeProp) {
	const _type = typeProp && typeProp.split('|');
	if (_type.length > 1)
		return _type[1];
}

const view = ({ properties }) => {
	const { type, message } = properties;
	const icon = { info, warning };
	const messageType = getMessageType(type);
	const ariaProps = {
		tabIndex: 0,
		role: messageType === 'info' ? 'status' : 'alert'
	};
	return (
		<div className="sn-elements-filter-message" {...ariaProps} aria-live="polite">
			<div className="sn-elements-filter-message--icon-container" aria-hidden="true">
				<img src={icon[messageType]}/>
			</div>
			<div className="sn-elements-filter-message--title-container">
				<span>{title[messageType]}</span>
			</div>
			<div className="sn-elements-filter-message--text">
				<span>{message}</span>
			</div>
		</div>
	);
};

export default createCustomElement('sn-elementsfilter-message', {
	renderer: {
		type: snabbdom,
		view,
	},
	properties: {
		type: {
			reflect: true,
			default: 'info',
		},
		message: {
			reflect: true,
		},
	},
	styles,
});
