
import { CHART_TYPES, XENOLITH_CHART_TYPES } from '../constants';
/**
 * Transform chart type for each series config into a type accepted by Xenolith
 * @param {Object} seriesConfig - array of seriesConfig objects
 * @param {string} chartType - type which needs to be appended to the series config
 * @param {string} previousChartType - previous chart type
 * @return {Object} adaptedSeriesConfig - object containing adapted series configs
 */
export const adaptSeriesConfig = (seriesConfig, chartType, previousChartType) => {
	const adaptedSeriesConfig = {};

	// Change types of visualization only for datasets which are the same visualization as the charttype
	Object.keys(seriesConfig).forEach(key => {
		const config = seriesConfig[key];

		if (toXenolithChartType(config.type) === previousChartType) {
			adaptedSeriesConfig[key] = {
				...config,
				type: toParChartType(chartType)
			};
		} else
			adaptedSeriesConfig[key] = config;
	});

	return adaptedSeriesConfig;
};

/**
 * Transform chart type into a type accepted by Xenolith
 * @param {string} type - the type to be transformed
 * @return {string} type - transformed type
 */
export const toParChartType = (type) => {
	if(type === CHART_TYPES.STEP_LINE)
		return XENOLITH_CHART_TYPES.STEP;

	if(type === CHART_TYPES.COLUMN)
		return XENOLITH_CHART_TYPES.GROUPED_BAR;

	if(type === CHART_TYPES.BAR || type === CHART_TYPES.HORIZONTAL_BAR)
		return XENOLITH_CHART_TYPES.GROUPED;

	if(type === CHART_TYPES.SEMI_DONUT)
		return XENOLITH_CHART_TYPES.SEMI_DONUT;

	return type;
};

/**
 * Transform xenolith type into a type accepted by the charts_data api
 * @param {string} type - the type to be transformed
 * @return {string} type - transformed type
 */
export const toXenolithChartType = (type) => {
	if(type === XENOLITH_CHART_TYPES.STEP)
		return CHART_TYPES.STEP_LINE;

	if(type === XENOLITH_CHART_TYPES.GROUPED_BAR)
		return CHART_TYPES.COLUMN;

	if(type === XENOLITH_CHART_TYPES.SEMI_DONUT)
		return CHART_TYPES.SEMI_DONUT;

	return type;
};
