import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-empty-state';
import '../snElementsFilterMessage/snElementsFilterMessage';
import { noPermissionMsg, contactAdminForPermissionMsg, contactAdminForInfoMsg } from '../snVisualization/snVisualization';

/**
 * View of the Visualization error component
 * @param  {Object} state
 */
const view = ({ properties }) => {
	const { error, error: { type = '' } } = properties;

	if (type.indexOf('elements_filter') === 0)
		return <sn-elementsfilter-message type={error.type} message={error.message} />;

	if (error.status === 403 || error.status === 401)
		return <now-empty-state type="permission-error" variant="fit" headerText={noPermissionMsg} bodyText={contactAdminForPermissionMsg} />;

	if ((error.status === 501) || (error.type === 'not-supported'))
		return <now-empty-state type="not-supported" variant="fit" />;

	const errorMessage = error.message? `${error.message}. `: '';
	const errorDetail = error.detail? `${error.detail}. `: '';

	return <now-empty-state type="loading-error" variant="fit" bodyText={`${errorMessage}${errorDetail}${contactAdminForInfoMsg}`} />;
};

export default createCustomElement('sn-component-visualization-error', {
	properties: {
		error: {
			default: null,
			reflect: false
		},
	},
	renderer: {
		type: snabbdom,
		view,
	},
});
