/**
* Function that builds the x formatter that adapts the trend-by reporting charts
* into timeseries by displaying the string categories on the X axis
* @param {array} xLabels - array of string categories
* Expects the timestampts of each series to be "0", "10", "20", etc...
* @returns {String} returned mapped xLabels string for every timestamp for
* example: for xLabels: ["(empty)", "2015", "2016", "2018", "2019"]
* it returns the following mapping:
* "0" -> "(empty)" index 0
* "10" -> "2015" index 1
* "20" -> "2016" index 2
* "30" -> "2018" index 3
* "40" -> "2019" index 4
* ...
*/
export const xAxisTrendFormatter = (xLabels = [], timestamp = '') => {

	// sometimes xenolith sends date object in timestamp, for example when there is only 1 data point available in series
	const timeStampResolved = (timestamp instanceof Date) ? timestamp.getMilliseconds().toString() : timestamp;

	// convert index (timestamp) to integer
	const timestampIndex = parseInt(timeStampResolved, 10);

	// get the right label for every timestamp
	if (xLabels[timestampIndex / 10])
		return xLabels[timestampIndex / 10];

	return null;
};
