import { CHART_TYPES, XENOLITH_CHART_TYPES } from '../constants';

export const isTimeSeries = chartType => [
	CHART_TYPES.TIMESERIES,
	CHART_TYPES.AREA,
	CHART_TYPES.LINE,
	CHART_TYPES.SPLINE,
	CHART_TYPES.STEP_LINE,
	CHART_TYPES.COLUMN,
].indexOf(chartType) > -1;

export const isBarType = chartType => chartType === CHART_TYPES.BAR || chartType === CHART_TYPES.HORIZONTAL_BAR || chartType === CHART_TYPES.HORIZONTAL_BAR_V2;
export const isPieDonut = chartType => chartType === CHART_TYPES.PIE || chartType === CHART_TYPES.DONUT || chartType === CHART_TYPES.SEMI_DONUT;
export const isHorizontalBar = chartType => chartType === CHART_TYPES.HORIZONTAL_BAR_V2 || chartType === CHART_TYPES.HORIZONTAL_BAR;

export const isGroupBy = chartType => isBarType(chartType) || isPieDonut(chartType) || isHorizontalBar(chartType);

export const isSingleScore = chartType => chartType === CHART_TYPES.SINGLE_SCORE;

/**
 * Checks if all series are timeseries-column
 * @param {Object} seriesConfig
 * @returns {Boolean}
 */
export const allSeriesAreTimeSeriesColumn = (seriesConfig) => {
	return Object.values(seriesConfig).filter((config) => config.type && (config.type.includes(CHART_TYPES.BAR) || config.type.includes(XENOLITH_CHART_TYPES.GROUPED_BAR))).length === Object.keys(seriesConfig).length;
};
