/**
 * Return the combined queries of filter with aggregate query
 * @param {*} aggregateQuery 
 * @param {*} filter 
 * @return {String}
 */
export const combineQueries = (aggregateQuery, filter) => {
	if (!filter)
		return aggregateQuery;
	if (!aggregateQuery)
		return filter;
	var filterQueries = filter.split('^NQ'); //this is required to handle UBER OR
	for (var i = 0; i < filterQueries.length; i++)
		filterQueries[i] += `^${aggregateQuery}`;
	return filterQueries.join('^NQ');
};