import isEmpty from 'lodash/isEmpty';

/**
* Builds styling configurations based on the passed styleConfigurations property
* @param {Object} styleConfigurations - user-defined Xenolith-compliant style configurations coming from properties (non-persisted)
* @param {Object} state - Payload
* @returns {Object} object with styling properties to be spread on the object that actually gets passed to Xenolith library
*/
export const buildStyleConfigs = (styleConfigurations, state = {}) => {
	const {
		seriesConfig = {},
		annotationsData = [],
		annotationsConfig = {}
	} = state;

	if (!isEmpty(styleConfigurations) && !isEmpty(seriesConfig)) {
		const styles = {
			seriesConfig,
			annotationsData,
			annotationsConfig
		};

		for (const styleProperty in styleConfigurations) {
			if (styleConfigurations.hasOwnProperty(styleProperty)) {
				// handle separately the dataset configuration styling property
				if (styleProperty === 'datasetConfig') {
					// get dataset config from the passed style configurations prop
					styles.datasetConfig = { ...styleConfigurations.datasetConfig };

					// set the 'for' property value immutably on every series config
					Object.keys(styleConfigurations.datasetConfig).forEach((datasetKey) => {
						// datasets are usually named with a prefix 'data_0'. in order to have config for the whole dataset
						// and not just one series, we check for this prefix
						Object.keys(styles.seriesConfig).filter(key => key.startsWith(datasetKey))
							.forEach((key) => {
								if (styles.seriesConfig[key])
									styles.seriesConfig[key].for = datasetKey;
							});
					});
				} else if (styleProperty === 'seriesConfig') {
					// Append series config to the config coming from the API
					Object.keys(styleConfigurations.seriesConfig).forEach((seriesKey) => {
						if(styles.seriesConfig[seriesKey]) {
							styles.seriesConfig[seriesKey] = {
								...styles.seriesConfig[seriesKey],
								...styleConfigurations.seriesConfig[seriesKey]
							};
						}
					});
				} else if (styleProperty === 'annotationsData') {
					// Append annotations data to the data coming from the API
					styles.annotationsData = [
						...styles.annotationsData,
						...styleConfigurations.annotationsData
					];
				} else if (styleProperty === 'annotationsConfig') {
					// Append annotations config to the config coming from the API
					styles.annotationsConfig = {
						...styles.annotationsConfig,
						...styleConfigurations.annotationsConfig
					};
				} else
					// just add the passed styles to the top level of Xenolith config object
					styles[styleProperty] = styleConfigurations[styleProperty];
			}
		}

		return styles;
	}

	return {};
};
