import { actionTypes } from '@servicenow/ui-core';
import adaptConfigType from '../adapters/adapt-config-type';
import { isEqual, omit } from 'lodash';
import { isGroupBy, isSingleScore, isTimeSeries } from '../helpers/chart-types';
import { toParChartType, adaptSeriesConfig } from '../adapters/adapt-chart-type';
import { shouldRefreshData } from '../helpers/should-refresh-data';

import {
	CHARTS_DATACONFIG_REQUESTED,
	RECORD_WATCHER_METADATA_REQUESTED,
	RECORD_WATCHER_SUBSCRIBED,
	SERIES_FETCH_DATACONFIG_REQUESTED,
	SERIES_FETCH_REQUESTED,
	AUTO_REFRESH_SUBSCRIBE,
	AUTO_REFRESH_UNSUBSCRIBE,
	FETCH_SERIES
} from '../constants';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_DISCONNECTED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const handleComponentDisconnected = ({ action, dispatch, state, updateState }) => {
	unSubscribeRealTimeData({ action, dispatch, state, updateState });
	dispatch(AUTO_REFRESH_UNSUBSCRIBE);
};

// Adapt style configurations in case data hasn't changed
const adaptStyleChanges = ({ action: { payload: { name, value, previousValue } }, state, updateState }) => {
	if(name === 'chartType')
		handleChartTypeChange(value, previousValue, state, updateState);

	if(name === 'variant')
		handleVariantChange(value, previousValue, state, updateState);
};

// When variant size changes, the chart should be re-rendered, in order to resize correctly
const handleVariantChange = (value, previousValue, state, updateState) => {
	if(value === previousValue)
		return;

	updateState({
		...state,
		stopRender: true
	});

	// Allow the component to render after 50 milliseconds, in order to re-render the chart
	setTimeout(() => {
		updateState({
			...state,
			stopRender: false
		});
	}, 50);
};

// The chart type should be configured correctly when switching between types in the same chart family
const handleChartTypeChange = (value, previousValue, state, updateState) => {
	if(value === previousValue)
		return;

	if (isTimeSeries(value)) {
		const {
			payload
		} = state;

		if (payload) {
			const seriesConfig = adaptSeriesConfig(payload.seriesConfig, value, previousValue);

			updateState({
				...state,
				chartType: value,
				payload: {
					...state.payload,
					seriesConfig,
				}
			});
		}
	}

	if (isGroupBy(value)) {
		// In case of group by charts, only the global type should be changed
		updateState({
			...state,
			chartType: value,
			payload: {
				...state.payload,
				type: toParChartType(value)
			}
		});
	}
};

// run when component's property changes
const handlePropertyChanged = ({ action, dispatch, state, updateState }) => {
	const {
		name,
		previousValue,
		value
	} = action.payload;

	let dispatcherConfiguration;

	if (name === 'forceRefresh' && value) {
		fetchData({ dispatch, state, updateState });

		//Unsubscribe from AMB because the data needs to be refetched
		if(state.channelId)
			unSubscribeRealTimeData({ dispatch, state, updateState });
	} else {
		if (name === 'sysId')
			dispatcherConfiguration = fetchData({ dispatch, state, updateState });
		else {
			if (shouldRefreshData(name, previousValue, value)) {
				dispatcherConfiguration = fetchData({ dispatch, state, updateState });

				//Unsubscribe from AMB because the data needs to be refetched
				if (state.channelId)
					unSubscribeRealTimeData({ dispatch, state, updateState });

			} else
				adaptStyleChanges({ action, state, updateState });
		}
	}

	handleRealTimePropertyChange({ action, dispatch, state, updateState }, dispatcherConfiguration);
	handleAutoRefreshPropertyChange({ action, dispatch, state, updateState }, dispatcherConfiguration);
};

// fetches the data based on the action
const fetchData = ({ dispatch, state, updateState }) => {
	const {
		dataConfigurations,
		filterConfigurations,
		sysId,
		chartType,
		isRealtime
	} = state.properties;
	const dispatcherConfiguration = {};

	if (sysId) {
		// persisted charts, with sysId
		dispatcherConfiguration.dispatcherActionPayload = { sysId };
		dispatcherConfiguration.dispatcherActionName = SERIES_FETCH_REQUESTED;
	} else {

		// dataConfig passed, no sysId
		if (dataConfigurations && dataConfigurations.length) {
			let dataConfigurationsReqBody;

			if (chartType) {
				// if chartType is passed hit the newer PAR API's Chart data endpoint
				dataConfigurationsReqBody = {
					data_configurations: dataConfigurations,
					filter_configurations: filterConfigurations,
					chart_type: chartType,
					realtime: isRealtime
				};

				dispatcherConfiguration.dispatcherActionPayload = { dataConfigurationsReqBody };
				dispatcherConfiguration.dispatcherActionName = CHARTS_DATACONFIG_REQUESTED;
			} else {
				// otherwise use the PAR API v2
				dataConfigurationsReqBody = { data_configurations: adaptConfigType(dataConfigurations) };

				dispatcherConfiguration.dispatcherActionPayload = { dataConfigurationsReqBody };
				dispatcherConfiguration.dispatcherActionName = SERIES_FETCH_DATACONFIG_REQUESTED;
			}
		} else {
			updateState({
				...state,
				loading: false,
				error: false
			});

			return;
		}
	}

	dispatch(FETCH_SERIES, dispatcherConfiguration);

	return dispatcherConfiguration;
};

const handleRealTimePropertyChange = ({ action: { payload: { name } }, dispatch, state, updateState }, dispatcherConfiguration) => {
	const {
		properties: { isRealtime, sysId },
		chartType
	} = state;

	if (name !== 'isRealtime' || !isSingleScore(chartType))
		return;

	// as of now realtime is only supported with sys-id
	if (!sysId)
		return;

	if (`${isRealtime}` === 'true')
		dispatch(RECORD_WATCHER_METADATA_REQUESTED, { sysId }, { sysId });
	else {
		unSubscribeRealTimeData({ dispatch, state, updateState });

		// fetch the series again
		dispatch(SERIES_FETCH_REQUESTED, { sysId }, { sysId });

		// if realtime is false and at the same time autorefresh is also > 0, make everyone happy by auto-refreshing.
		dispatch(AUTO_REFRESH_SUBSCRIBE, { ...dispatcherConfiguration }, { ...dispatcherConfiguration });
	}
};

const handleAutoRefreshPropertyChange = ({ action: { payload: { name, value, previousValue } }, dispatch, state }, dispatcherConfiguration) => {
	const {
		properties: { isRealtime },
		chartType
	} = state;

	// We should only resubscribe for those properties regarding data fetch, and auto refresh itself. NO others.
	if (name !== 'refreshFrequency' && name !== 'dataConfigurations' && name !== 'filterConfigurations' && name !== 'chartType')
		return;

	// if realtime is true then auto-refresh should never be called.
	if (isEqual(value, previousValue) || `${isRealtime}` === 'true' || !isSingleScore(chartType))
		return;

	dispatch(AUTO_REFRESH_UNSUBSCRIBE);
	dispatch(AUTO_REFRESH_SUBSCRIBE, { ...dispatcherConfiguration }, { ...dispatcherConfiguration });
};

const unSubscribeRealTimeData = ({ dispatch, state, updateState }) => {
	const { channelId, datasets } = state;

	if (channelId) {
		dispatch(RECORD_WATCHER_SUBSCRIBED, {
			channelId,
			subscribe: false,
		});

		if(Array.isArray(datasets) && datasets.length)
			datasets[0].score_details = omit(datasets[0].score_details, ['last_updated', 'is_realtime']);

		const lastSynched = new Date();
		updateState({
			...state,
			channelId: '',
			datasets,
			lastSynched
		});
	}
};


export default {
	[COMPONENT_BOOTSTRAPPED]: fetchData,
	[COMPONENT_PROPERTY_CHANGED]: handlePropertyChanged,
	[COMPONENT_DISCONNECTED]: handleComponentDisconnected,
};
