import { POINT_CLICKED } from '../constants';
import { get } from 'lodash';

/**
 * Handles click behavior for the single score. It prepares the payload and dispatches an event.
 */
const handleScoreClicked = ({ action, dispatch, state }) => {
	const {
		dataConfigurations
	} = state.properties;

	const dataConfiguration = get(dataConfigurations, '[0]', {});
	let clickPayload;

	if (dataConfiguration.source_type === 'table' || dataConfiguration.source_type === 'source') {
		const {
			filterQuery,
			tableName,
			type,
		} = state.payload;

		clickPayload = {
			source_type: dataConfiguration.source_type,
			source_id: dataConfiguration.source_id,
			type,
			table: tableName,
			conditions: filterQuery,
		};
	} else {
		const {
			uuid
		} = state.payload;

		clickPayload = {
			uuid
		};
	}

	action.stopPropagation();
	dispatch(POINT_CLICKED, clickPayload);
};

export default {
	['NOW_SCORE#CLICKED']: handleScoreClicked,
};
