import { i18n } from '../i18n.js'; // i18n file is auto-generated by scripts/tectonic_i18n.js
import { i18nHelper, dateHelper } from 'par-components';
import { t } from 'sn-translate';
import { createCustomElement } from '@servicenow/ui-core';
import react from '@servicenow/ui-renderer-react';
import { preUserPrefs, getProperty } from 'sn-uxpage-presource';
import visibilityBehavior from 'visibility-behavior';
import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import adaptXenolith from './adapters/adapt-xenolith';
import { getInteractions, dispatchClickAction } from './interactions';
import { isTimeSeries, isPieDonut, isBarType, isHorizontalBar } from './helpers/chart-types';

// Helpers
import { getAttributeValue } from './helpers/domUtil';
import { injectPrefs } from './helpers/inject-user-prefs';

// Adapters
import { adaptSingleScore, isNoScore } from './adapters/adapt-single-score';

// Constants
import {
	CHART_TYPES,
	SERIES_FETCH_REQUESTED,
	ELEMENT_VISIBLE,
	GLIDE_SYSTEM_LOCALE,
	SET_HOST_REF,
} from './constants';

// Action handlers
import { seriesHandlers, recordWatcherHandlers, lifeCycleHandlers, singleScoreClickHandlers, autoRefreshHandlers } from './handlers';

// Styles
import styles from './snVisualization.scss';
// TODO: remove parStyles after fully migrating to the NDS Single score
import parStyles from 'par-visualization/dist/seismic.bundle.css';

// Components
import '@servicenow/now-empty-state';
import '@servicenow/now-chart-timeseries';
import '@servicenow/now-chart-bar';
import '@servicenow/now-chart-donut-pie';
import '@servicenow/now-score';
import '../snVisualizationList/snVisualizationList';
import '../snVisualizationError/snVisualizationError';
import '../snVisualCue/snVisualCue';
import { Visualization } from 'par-visualization/seismic'; // eslint-disable-line no-unused-vars

//TODO: There is an issue with proxying AMB for RW in dev mode, we have to remove it in future
if (process.env.NODE_ENV === 'development')
	delete window.WebSocket;

export const noPermissionMsg = t('You need to get permission to see this data');
export const contactAdminForPermissionMsg = t('Contact your ServiceNow administrator for permission.');
export const contactAdminForInfoMsg = t('Contact your ServiceNow administrator for more information.');
const notConfiguredMsg = t('This data visualization is not configured yet.');
const noContentMsg = t('No content available');
const noDataMsg = t('No data available');
const noScoresMsg = t('There are no scores available for the indicator or for the selected criteria.');

// Render the visualization component TODO: Externalize renderVisualization function, beware of the React renderer dependency of Visualization component
const renderVisualization = state => {
	const {
		properties,
		fetchedBySysId,
		dispatch,
		payload,
		isAmbConnectionBroken,
		chartType,
		stopRender
	} = state;

	if (stopRender)
		return null;

	// If there is a sysId, but the request has not run yet, then fetch the series and then render the loading state
	if (properties.sysId && fetchedBySysId !== properties.sysId)
		return (<now-empty-state type="loading" widget={t('Visualization')} variant="fit" />);

	// If not configured yet in UIB
	if (!properties.sysId && !properties.dataConfigurations && !properties.datasets)
		return (<now-empty-state type="empty" variant="fit" headerText={noContentMsg} bodyText={`${notConfiguredMsg} ${contactAdminForInfoMsg}`} />);

	// If there is no data to display
	if (!payload && !(state.datasets || properties.datasets))
		return (<now-empty-state type="empty" variant="fit" headerText={noDataMsg} bodyText={`${noScoresMsg} ${contactAdminForInfoMsg}`} />);

	// No data in seriesData
	if (payload && chartType !== CHART_TYPES.SINGLE_SCORE && isEmpty(payload.seriesData))
		return (<now-empty-state type="empty" variant="fit" headerText={noDataMsg} bodyText={`${noScoresMsg} ${contactAdminForInfoMsg}`} />);

	// Edge case when just a tiny subset of the widget/report is not supported it's difficult to throw 501 due to streaming.
	if (payload && payload.type === 'not-supported')
		return (<now-empty-state type="not-supported" variant="fit" />);

	if (isTimeSeries(chartType)) {
		return (<now-chart-timeseries
			interactions={getInteractions()}
			{...adaptXenolith(payload, chartType, properties.styleConfigurations)}
		/>);
	}

	if (isBarType(chartType)) {
		return (<now-chart-bar
			interactions={getInteractions()}
			orientation={isHorizontalBar(chartType) ? 'horizontal' : 'vertical'}
			{...adaptXenolith(payload, chartType, properties.styleConfigurations)}
		/>);
	}

	if (isPieDonut(chartType)) {
		return (<now-chart-donut-pie
			interactions={getInteractions(false)}
			{...adaptXenolith(payload, chartType, properties.styleConfigurations)}
		/>);
	}

	// FOR NON-XENOLITH chart types: LIST and SINGLE SCORE
	const {
		dataConfigurations,
		styleConfigurations,
		datasets,
	} = (state.datasets || state.dataConfigurations ? state : properties); // These are both in the state and in the props

	// Get the first data configuration to check whether it's a list or not
	const dataConfiguration = get(dataConfigurations, '[0]', {});
	if (chartType === CHART_TYPES.LIST) {
		return (
			<sn-component-visualization-list
				table={dataConfiguration.table_name}
				conditions={dataConfiguration.filter_query}
				groupByField={dataConfiguration.group_by_field}
				columns={dataConfiguration.field_list}
				variant={properties.variant}
			/>
		);
	}

	if (state.chartType === CHART_TYPES.SINGLE_SCORE && payload && !datasets) {
		// No data in single score
		if (isNoScore(payload.score))
			return (<now-empty-state type="empty" variant="fit" headerText={noDataMsg} bodyText={`${noScoresMsg} ${contactAdminForInfoMsg}`} />);

		const adaptedSingleScore = adaptSingleScore(payload, properties.styleConfigurations, state.targetRef);
		if (isEmpty(payload.changeInfo) && isEmpty(payload.targetInfo) && isEmpty(payload.sparkline))
			return (<now-score-basic {...adaptedSingleScore} />);

		return (<now-score-advanced {...adaptedSingleScore} />);
	}

	// If there are no data configurations or no datasets (also no sysId, but
	// that is checked before the code reaches this point), then the component has not yet been configured
	if (isEmpty(dataConfigurations) && isEmpty(datasets) && isEmpty(styleConfigurations))
		return (<now-empty-state type="empty" variant="fit" headerText={noContentMsg} bodyText={`${notConfiguredMsg} ${contactAdminForInfoMsg}`} />);

	// Payload for the click action
	let clickPayload = {
		source_type: dataConfiguration.source_type,
		source_id: dataConfiguration.source_id,
		type: dataConfiguration.type
	};

	if (dataConfiguration.source_type === 'table' || dataConfiguration.source_type === 'source') {
		clickPayload = {
			...clickPayload,
			table: dataConfiguration.table_name,
			conditions: dataConfiguration.filter_query,
			view: dataConfiguration.list_ui_view
		};
	}

	// inject the user/system preferences into the styleConfigurations
	const styleConfigurationsWithPrefs = injectPrefs(
		[{
			...(isEmpty(styleConfigurations) ? {
				chart_type: CHART_TYPES.SINGLE_SCORE,
				show_zero: true,
			} : styleConfigurations[0]),
			...properties.styleConfigurations
		}],
		{
			locale: getProperty(GLIDE_SYSTEM_LOCALE),
			userPrefs: preUserPrefs.getPreSource(state, {}),
		},
	);

	// Finally, render the React SINGLE SCORE visualization with the passed props (and by overwriting the click behaviours)
	// We only send the dataConfigurations coming from the properties, not the ones that the API receives
	return (
		<Visualization
			dataConfigurations={properties.dataConfigurations}
			styleConfigurations={styleConfigurationsWithPrefs}
			datasets={datasets}
			clickBehaviors={
				dispatchClickAction(dispatch, clickPayload, {
					state,
					dataConfigurations,
					styleConfigurations: styleConfigurationsWithPrefs,
				})
			}
			chartLibrary="xenolith"
			stateComponents={{
				empty: (<now-empty-state type="empty" variant="fit" />),
				unsupported: (<now-empty-state type="not-supported" variant="fit" />),
				needsPermission: (<now-empty-state type="permission-error" variant="fit" headerText={noPermissionMsg} bodyText={contactAdminForPermissionMsg} />),
				error: (<now-empty-state type="loading-error" variant="fit" />),
				loading: (<now-empty-state type="loading" widget={t('Visualization')} variant="fit" />),
				realtimeWarning: (<sn-visual-cue show={isAmbConnectionBroken} />),
			}}
		/>
	);
};

const renderRealtimeTitle = (state) => {
	const {
		chartType,
		lastUpdated,
		datasets,
		isAmbConnectionBroken,
		properties: {
			isRealtime,
			showRealtimeIndicator
		}
	} = state;

	if (!showRealtimeIndicator || chartType !== CHART_TYPES.SINGLE_SCORE || !isRealtime || datasets)
		return null;

	return lastUpdated ? (
		<h3 className="last-updated">
			{t('At')}
			<time dateTime={lastUpdated}> {lastUpdated}</time>
			{isAmbConnectionBroken && <sn-visual-cue show={isAmbConnectionBroken} />}
		</h3>
	) : null;
};

/**
 * Main view of the component which renders all the subviews
 * @param  {Object} state
 */
const view = state => {
	const {
		error,
		loading,
		lastSynched
	} = state;

	return (
		<div className="sn-visualization">
			{loading && <now-empty-state type="loading" widget={t('Visualization')} variant="fit" />}
			{error && <sn-component-visualization-error error={error} />}
			{!loading && !error &&
				<div className="sn-visualization-content-container">
					{lastSynched && renderRealtimeTitle(state)}
					{renderVisualization(state)}
				</div>
			}
		</div>
	);
};

/**
 * Visualization component for displaying charts based on Performance Analytics and Reporting data sources.
 * It also supports visualizing your own custom datasets that can be passed in as properties along with style configurations for single score chart.
 *
 * ```
 * <sn-component-visualization dataConfigurations={dataConfigurations} styleConfigurations={styleConfigurations}/>
 * 		OR
 * <sn-component-visualization datasets={datasets} styleConfigurations={styleConfigurations} />
 * 		OR
 * <sn-component-visualization sysId={sysId} />
 * ```
 * @seismicElement sn-component-visualization
 * @summary Performance Analytics and Reporting visualization component.
 */
export default createCustomElement('sn-component-visualization', {
	renderer: {
		type: react,
		view,
	},
	properties: {
		/**
		 * Array of datasets containing series of data to be visualized. For SINGLE SCORE only.
		 *
		 * @type {Array}
		 */
		datasets: {
			reflect: false
		},
		/**
		 * Object or array (for old single score) containing Xenolith styleConfigurations, or any other XN configuration
		 * that needs to override the default settings
		 * @type {Object|Array}
		 */
		styleConfigurations: {
			reflect: false,
			schema: {
				type: 'object',
			},
		},
		/**
		 * String representing the sys_id of an existing report or PA widget to be retrived from PAR API.
		 * @type {String}
		 */
		sysId: {
			reflect: true,
			onChange(sysId, previousSysId, dispatch) {
				if (sysId)
					// We pass the sysId both as payload and meta (check the onConnect method for the reason behind that)
					dispatch(SERIES_FETCH_REQUESTED, { sysId }, { sysId });
			}
		},
		/**
		 * Type of visualizaiton. If passed, the data will be fetched from the /now/charts/data endpoint.
		 * @type {String}
		 */
		chartType: {
			reflect: false,
		},
		/**
		 * Array of objects containing data configuration settings for non-persisted data fetching from API.
		 * @type {Array}
		 */
		dataConfigurations: {
			reflect: false,
			schema: {
				type: 'array',
			},
		},
		/**
		 * Array of objects containing filter configuration settings for non-persisted data fetching from API.
		 * @type {Array}
		 */
		filterConfigurations: {
			reflect: false,
			schema: {
				type: 'array',
			},
		},
		/**
		 * Size variant for the List report. Possible string values: 'SMALL', 'MEDIUM', 'LARGE', 'XL'
		 * @type {('SMALL'|'MEDIUM'|'LARGE'|'XL')}
		 */
		variant: {
			reflect: true,
			default: 'SMALL',
		},
		/**
		 * Number of milliseconds for the refreshing interval (e.g. on Workspace)
		 * @type {Number}
		 */
		refreshInterval: {
			reflect: true,
		},
		/**
		 * TODO: Remove support for this property after migrating to the NDS single score
		 * If enabled, autoClickBehavior will allow for dispatching SN_VISUALIZATION#POINT_CLICKED action with a specific datapoint's payload when clicking on a time series chart.
		 * @type {Boolean}
		 */
		autoClickBehavior: {
			reflect: true,
			default: false,
		},
		/**
		 * Force data refresh of the visualization
		 * @type {Boolean}
		 */
		forceRefresh: {
			default: false
		},
		/**
		 * If enabled then we will make the component accept real time updates from AMB
		 * NOTE: Passing it to true does not guarantees that widget will be real time widget.
		 * @type {Boolean}
		 */
		isRealtime: {
			reflect: true,
			default: false,
		},
		/**
		 * If enabled the visual indicator will be displayed when real time update happens
		 * @type {Boolean}
		 */
		showRealtimeIndicator: {
			reflect: true,
			default: true,
		},
		/**
		 * String of seconds for the auto refresh
		 * @type {String}
		 */
		refreshFrequency: {
			reflect: true,
			default: '0',
		},
		/**
		 * [DEPRECATED]
		 * If enabled, on-click behavior
		 * if defined will be overridden to
		 * navigate to KPI Details view
		 */
		kpiDetailsNavigation: {
			default: false
		}
	},
	styles: styles + parStyles,
	initialState: {
		isAmbConnectionBroken: false,
		loading: true
	},
	onConnect(host, dispatch) {
		// inject seismic i18n resolved dictionary in our own
		i18nHelper.setDictionary(i18n().values);

		// This is set by UXBuilder (document.documentElement.lang)
		dateHelper.setLocale(document.documentElement.lang);

		/**
		 * we need host element ref to do some cleanup for auto-refresh
		 * since this comp uses react renderer so can't use hook-insert
		 * TODO: Please remove this and use snabdom hook when available
		 **/
		dispatch(SET_HOST_REF, { targetRef: host });
	},
	behaviors: [
		{
			behavior: visibilityBehavior,
			options: {
				intervalPropName: 'refreshInterval',
				action: ELEMENT_VISIBLE,
			}
		},
		preUserPrefs,
	],
	actionHandlers: {
		...seriesHandlers,
		...recordWatcherHandlers,
		...lifeCycleHandlers,
		...singleScoreClickHandlers,
		...autoRefreshHandlers
	}
});
