import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-button';
import { t } from 'sn-translate';

// Styles
import styles from './snVisualizationList.scss';

// Components
import '@servicenow/now-record-list-connected';

/**
 * The view of the wrapper of the List component
 *
 * @param  {Object} state
 */
const view = (state) => {
	const { properties, listCount } = state;
	const { table, conditions, groupByField, columns, variant } = properties;

	const limits = {
		SMALL: 10,
		MEDIUM: 10,
		LARGE: 10,
		XL: 15
	};

	const maxCols = {
		SMALL: 3,
		MEDIUM: 5,
		LARGE: 7,
		XL: 7
	};

	const displayShowAll = listCount > 0;

	let filterQuery = conditions;
	if (groupByField)
		filterQuery = filterQuery ? `${filterQuery}^GROUPBY${groupByField}` : `GROUPBY${groupByField}`;

	return (
		<div className={`sn-visualization-list -size-${variant.toLowerCase()}`}>
			<div className="list-container">
				<now-record-list-connected
					table={table}
					columns={columns}
					query={filterQuery}
					limit={limits[variant] || undefined}
					max-columns={maxCols[variant] || undefined}
					hide-pagination={true}
					hide-cell-filter={true}
					hide-row-selector={true}
					hide-header={true}
					hide-quick-edit={true}
					hide-column-filtering={true}
					hide-column-sorting={true}
				/>
			</div>
			{ displayShowAll ? (
				<div className="show-all-container">
					<now-button-bare label={t('Show all')} size="md" variant="secondary" />
				</div>
			) : null }
		</div>
	);
};

export default createCustomElement('sn-component-visualization-list', {
	renderer: {
		type: snabbdom,
		view,
	},
	properties: {
		table: {
			reflect: true,
		},
		conditions: {
			reflect: true,
		},
		groupByField: {
			reflect: true,
		},
		columns: {
			reflect: true,
		},
		variant: {
			reflect: true,
			default: 'SMALL',
		},
	},
	initialState: {
		listCount: 0
	},
	actionHandlers: {
		'NOW_BUTTON_BARE#CLICKED': {
			effect(coeffects) {
				const { dispatch, state } = coeffects;
				const { table, conditions, columns } = state.properties;
				dispatch('SN_VISUALIZATION#LIST_VIEW_ALL_CLICKED', {
					table, conditions, columns
				});
			},
			stopPropagation: true
		},
		LIST_DATA_FETCH_COMPLETE: {
			effect({ state, action, updateState }) {
				updateState({
					...state,
					listCount: action.payload.count
				});
			}
		}
	},
	styles: [styles],
});
