/**
 * Returns the default formatters config to be used if there is no axis config. Also, provides backwards
 * compatibility with the previous versions of the API
 * @param formattersConfig
 * @returns {object}
 */
export const defaultFormattersConfig = (formattersConfig = {}) => {
	if(formattersConfig.data_0_0)
		return formattersConfig.data_0_0;

	return formattersConfig;
};
