import { buildXFormat } from '../formatters/build-x-format';
import { buildYFormat } from '../formatters/build-y-format';
import { buildTooltip } from '../formatters/build-tooltip';
import seriesConfigFormatter from '../formatters/seriesconfig-formatter';
import { buildStyleConfigs } from '../formatters/build-style-configs';
import { allSeriesAreTimeSeriesColumn, isBarType, isPieDonut, isTimeSeries } from '../helpers/chart-types';
import { payloadBuilder } from '../helpers/payload-builder';
import { defaultFormattersConfig } from '../formatters/default-formatters-config';

/**
 * Return adapted PAR REST API v2 response payload for Xenolith charting library.
 * @param {Object} [payload = {}] REST payload to be adapted for passing into Xenolith library
 * @param {String} chartType chart type
 * @param {Object} styleConfigurations styles coming from properties
 * @returns {Object}
 */
export default (payload = {}, chartType, styleConfigurations) => {
	const hasSingleYAxis = payload.yAxisConfig && (
		(isTimeSeries(chartType) && (Object.keys(payload.yAxisConfig).length === 1)
		|| allSeriesAreTimeSeriesColumn(payload.seriesConfig))
		|| isBarType(chartType)
		|| isPieDonut(chartType)
	);

	const seriesConfig = {};

	// Add default values to all series config
	if (payload.seriesConfig) {
		Object.keys(payload.seriesConfig).forEach((key) => {
			seriesConfig[key] = {
				...payload.seriesConfig[key],
				...(isPieDonut(chartType) ? { maxSlices: 0 } : {}),
				...(seriesConfigFormatter(payload.seriesConfig[key])),
			};
		});
	}

	const mainFormatterConfig = defaultFormattersConfig(payload.formattersConfig);

	return payloadBuilder(payload)
		.add(() => buildXFormat(mainFormatterConfig), isTimeSeries(chartType))
		.add(() => buildTooltip(mainFormatterConfig, payload.targetsData, isTimeSeries(chartType)))
		.add(() => ({ yAxisConfig: payload.yAxisConfig.y }), !!(hasSingleYAxis && payload.yAxisConfig.y))
		.add(() => ({ extents: { y: payload.extents.y } }), !!(hasSingleYAxis && payload.extents && payload.extents.y))
		.add(() => ({ orderBy: 'dataOrder' }), isPieDonut(chartType) || isBarType(chartType))
		.add(() => ({ seriesConfig }))
		.add((state) => buildStyleConfigs(styleConfigurations, state), styleConfigurations)
		.add((state) => buildYFormat(state, hasSingleYAxis))
		.build();
};
