/**
 * Format series config properties.
 * (Currently used only for escaping HTML labels to prevent XSS)
 * @param {Object} seriesConfig
 */
export default seriesConfig => {
	const { label } = seriesConfig;

	if (label && label.includes('<')) {
		return {
			label: label.replace(/</g, '&lt;')
		};
	}

	return {};
};
