import {
	AUTO_REFRESH_SUBSCRIBE,
	AUTO_REFRESH_UNSUBSCRIBE,
	FETCH_SERIES_IF_POSSIBLE,
	SET_HOST_REF,
} from '../constants';

import { getParsedFrequencyInMillis } from '../helpers/domUtil';
import { isVisible } from 'visibility-behavior';

export default {
	[AUTO_REFRESH_SUBSCRIBE]: {
		effect({
			action: { payload },
			updateState,
			dispatch,
			state: {
				timerId,
				properties: { refreshFrequency },
			},
		}) {
			const refreshFrequencyInMillis = getParsedFrequencyInMillis(
				refreshFrequency
			);
			if (!timerId && refreshFrequencyInMillis) {
				const timerId = setInterval(() => {
					dispatch(FETCH_SERIES_IF_POSSIBLE, { ...payload });
				}, refreshFrequencyInMillis);
				updateState({ timerId });
			}
		},
	},
	[FETCH_SERIES_IF_POSSIBLE]: {
		effect({
			action: { payload },
			state: { isRequestCompleted, targetRef },
			updateState,
			dispatch,
		}) {
			// DEF0100415 - in workspace when we go to other module/tab still in dom sn-comp-viz will be there,
			// now we will fire the network call only when its visible.
			const isRootElementVisible =
				targetRef && isVisible(targetRef.getBoundingClientRect());
			if (
				isRootElementVisible &&
				isRequestCompleted &&
				payload.dispatcherActionName
			) {
				const config = {
					...payload.dispatcherActionPayload,
					hideLoading: true,
				};
				dispatch(payload.dispatcherActionName, config, config);
				updateState({ isRequestCompleted: false });
			}
		},
	},
	[AUTO_REFRESH_UNSUBSCRIBE]: {
		effect({ state: { timerId }, updateState }) {
			if (timerId) {
				clearInterval(timerId);
				updateState({ timerId: '' });
			}
		},
	},
	[SET_HOST_REF]: {
		effect({
			action: {
				payload: { targetRef },
			},
			updateState,
		}) {
			if (targetRef) updateState({ targetRef });
		},
	},
};
