import { t } from 'sn-translate';
import { buildXFormat } from './build-x-format';
import { getReadableDate } from '../helpers/get-readable-date';

/**
 * Builds the tooltipConfig and showTooltip properties
 * @param {object} formattersConfig - config options coming from the REST API
 */
export const buildTooltip = (formattersConfig = {}, targetsData = [], xFormat) => {
	const tooltipFormattersConfig = {
		...formattersConfig,
		isTooltip: true,
		type: 'long', // length of the timestamp
	};

	const startDateText = t('Target start date');
	const reviewDateText = t('Target review date');

	const config = {
		showTooltip: true
	};

	if(xFormat) {
		if (targetsData.length) {
			config.tooltipConfig = {
				xFormat (timestamp) {
					if (targetsData.filter(data => data.isStartDate && data.x === timestamp).length)
						return `${getReadableDate(timestamp, tooltipFormattersConfig)} - ${startDateText}`;

					if (targetsData.filter(data => data.isReviewDate && data.x === timestamp).length)
						return `${getReadableDate(timestamp, tooltipFormattersConfig)} - ${reviewDateText}`;

					return getReadableDate(timestamp, tooltipFormattersConfig);
				}
			};
		} else if (formattersConfig.xAxisLabels) {
			config.tooltipConfig = {
				xFormat (timestamp) {
					const currentLabel = formattersConfig.xAxisLabels.find(label => label.x == timestamp);
					return currentLabel && currentLabel.tooltipText;
				}
			};
		} else {
			config.tooltipConfig = {
				...buildXFormat(tooltipFormattersConfig)
			};
		}
	}

	return config;
};
