import { dateHelper } from 'par-components';
import { getValueWithUnit } from '../helpers/get-value-with-unit';
import { defaultFormattersConfig } from './default-formatters-config';

/**
 * Builds Y axis formatter function (wrapped in an object)
 * @param payload
 * @param {Object} payload - configuration object received from PAR REST API
 * @returns {Object} object with yFormat formatter callback for Xenoltih Y axis label formatting
 */
export const buildYFormat = (payload = {}, hasSingleYAxis) => {
	const {
		seriesConfig,
		yAxisConfig,
		formattersConfig = {},
	} = payload;
	let yFormat = {};

	// y axis must be configured already for each of the seriesConfig, if not, take the formatter from the first dataset
	const hasAxisConfiguration =
		yAxisConfig && Object.values(seriesConfig).every(config => config.axis && yAxisConfig[config.axis]);

	if (!hasSingleYAxis && hasAxisConfiguration) {
		// has multiple Y axes
		Object.keys(seriesConfig).forEach(key => {
			const formatterConfig = formattersConfig[key];
			const { axis } = seriesConfig[key];
			yFormat[axis] = getFormatter(formatterConfig);
		});
	} else
		yFormat = getFormatter(defaultFormattersConfig(formattersConfig));

	return { yFormat };
};

const getFormatter = (formattersConfig = {}) => {
	// if Y axis values represent duration in seconds, for reporting trends (Boolean)
	if (formattersConfig.yAxisDuration) {
		return (seconds) => {
			return dateHelper.getDurationValue(seconds * 1000);
		};
	}

	return (value) => {
		return getValueWithUnit(value, formattersConfig);
	};
};
