import { dateHelper } from 'par-components';

/**
 * Function takes the timestamp and formattersConfig as an input and
 * converts it in a readable format
 *
 * @param {any} timestamp
 * @param {Object} formattersConfig
 */
export const getReadableDate = (timestamp, formattersConfig) => {
	if (timestamp && typeof formattersConfig === 'object') {
		return dateHelper.readableDate(
			timestamp instanceof Date ? timestamp.getTime() : parseInt(timestamp),
			{
				...formattersConfig,
				type: formattersConfig.type || 'short',
			}
		);
	}
};
