import { xAxisTrendFormatter } from './x-axis-trend-formatter';
import { getReadableDate } from '../helpers/get-readable-date';

/**
* Builds X axis formatter function (wrapped in an object)
* @param {Object} formattersConfig - formatter configuration object received from PAR REST API
* @returns {Object} object with xFormat formatter callback or string for Xenoltih X axis label formatting
*/
export const buildXFormat = (formattersConfig = {}) => {
	// formatting only if xLabels are returned from server (only for Reporting Trends)
	if (formattersConfig.xLabels) {
		return {
			xFormat(timestamp) {
				return xAxisTrendFormatter(formattersConfig.xLabels, timestamp);
			}
		};
	}

	// formatting only if xAxisLabels are returned from server (for PA Business calendar support)
	if (formattersConfig.xAxisLabels) {
		return {
			xFormat(timestamp) {
				const currentLabel = formattersConfig.xAxisLabels.find(label => label.x == timestamp);
				return currentLabel && currentLabel.label;
			}
		};
	}

	// frequency based xFormat used for ITOM/Metricbase
	const { frequency, isTooltip } = formattersConfig;
	if (frequency === 'minutely') {
		// if it's not tooltip ITOM wants default Xenolith formatting
		if (!isTooltip)
			return {};

		return {
			// if it is tooltip, ITOM wants the following custom formatting
			xFormat: '%a %d, %B, %I:%M %p'
		};
	}

	// otherwise return xFormat that returns readable timestamp using date helper from PAR components
	return {
		xFormat(timestamp) {
			// when there is only one data point, xenolith passes timestamp as a date object (only for x-axis currently)
			return getReadableDate(timestamp, formattersConfig);
		}
	};
};
