import { isEqual } from 'lodash';
import { isGroupBy, isTimeSeries } from './chart-types';

/**
 * Checks whether there is a need to refresh the data on the chart. Data should be refreshed only in case
 * dataConfigurations, chart_type or filterConfigurations has changed
 * @param name - name of the property to be checked (ie. dataConfigurations...)
 * @param previousValue - value before the property has changed
 * @param value - value after the property has changed
 * @returns {boolean}
 */
export const shouldRefreshData = (name, previousValue, value) => {
	if (name === 'dataConfigurations' || name === 'filterConfigurations')
		return !isEqual(value, previousValue);

	if(name === 'chartType') {
		// If there is a change in chart family, data needs to be refreshed
		if (isTimeSeries(previousValue))
			return !isTimeSeries(value);

		if (isGroupBy(previousValue))
			return !isGroupBy(value);

		return true;
	}

	return (name === 'isRealtime' || name === 'refreshFrequency') && previousValue !== value;
};
