import shouldPassClickBehaviors from '../helpers/should-pass-click-behaviors';
import {
	POINT_CLICKED,
	POINT_CLICKED_XENOLITH,
} from '../constants';
import { parseUuid } from '../helpers/parse-uuid';

// Dispatch click action when the user clicks on the visualization
// USED ONLY FOR THE OLD SINGLE SCORE!
export const dispatchClickAction = (dispatch, clickPayload, data) => {
	if (!shouldPassClickBehaviors(data))
		return;

	if (clickPayload.source_type === 'analytics')
		clickPayload.uuid = parseUuid(clickPayload.source_id);

	return [(point = {}) => {
		dispatch(POINT_CLICKED, {
			...clickPayload,
			// If the point is an event, then don't attach it to the payload
			...(typeof point.preventDefault === 'function' ? {} : point),
		});
	}];
};

/**
* Return a collection of interactions in Xenolith format. Pies/Donuts do not have the processData function.
* @param {Boolean} hasProcessDataFn - true if processData function is required for a certain chart type
* @returns {Array}
*/
export const getInteractions = (hasProcessDataFn = true) => {
	return [
		{
			eventName: 'click',
			getCoordsFn: 'getMouseCoords',
			getDataFn: 'getClosestSeriesToMouse',
			dispatchName: POINT_CLICKED_XENOLITH
		},
		{
			eventName: 'mousemove',
			getCoordsFn: 'getMouseCoords',
			getDataFn: 'getClosestSeriesToMouse',
			...(hasProcessDataFn && { processDataFn: 'displayFoundData' }) // Xenolith doesn't support processDataFunction for pies and bars
		},
		{
			eventName: 'mouseout',
			getDataFn: 'clearData'
		}
	];
};
